html {
  font-size: 62.5%
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  outline: none;
  color: inherit
}

body {
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 1.6rem;
  line-height: 1.6;
  color: #666;
}

.board {
  display: grid;
  grid-template-columns: 30rem 1fr;
  grid-template-rows: 7rem 1fr;
  height: 100%;
}

.wrapper {
  min-height: calc(100vh - 20rem);
  display: grid;
  align-items: baseline;
}

.container {
  margin: 0 auto;

  @media (max-width: 1240px) {
    padding: 0 2rem;
    margin: 0;
  }
}